<template>
  <div class="map-container">
    <baidu-map class="map" :center="mapCenter" :zoom="zoom" @ready="mapReady">
      <!-- 可以添加地图控件 -->
      <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
      <bm-scale anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-scale>

      <!-- 添加标记点 -->
      <bm-marker :position="mapCenter"></bm-marker>
    </baidu-map>
    <div>
      <div>
        <span>经度</span>
        <input type="text" v-model="mapCenter.lng" />
      </div>
      <div>
        <span>纬度</span>
        <input type="text" v-model="mapCenter.lat" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mapCenter: null, // 北京市中心坐标
      zoom: 15
    }
  },
  methods: {
    mapReady({ BMap, map }) {
      console.log('百度地图加载完成', BMap, map)
      this.getCurrentLocation(BMap, map)
    },
    getCurrentLocation(BMap, map) {
      const geolocation = new BMap.Geolocation();
      geolocation.enableSDKLocation(); // 启用SDK辅助定位
      geolocation.getCurrentPosition(function (r) {
        if (this.getStatus() == 0) {
          this.mapCenter = {
            lng: r.point.lng,
            lat: r.point.lat
          };
          map.panTo(r.point);
          map.addOverlay(new BMap.Marker(r.point));
        }
      });
    }
  }
}
</script>

<style scoped>
.map-container {
  width: 100%;
  height: 400px;
}

.map {
  width: 100%;
  height: 100%;
}
</style>