import Vue from 'vue'
import BaiduMap from 'vue-baidu-map'
import App from './App.vue'
import router from './router'
import store from './store'
Vue.use(BaiduMap, {
  // 注意：这里需要使用你在百度地图开发者平台申请的 ak
  ak: 'Ua1M6L6mOK6qdEbjqlHlUz5L'
})
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
